h1,
h2,
h3,
ul,
li,
p {
      margin: 0;
      padding: 0;
      text-decoration: none;
    }

img {
 
    display: block;
    width: 100%;
    height: auto;
 }
li {list-style-type: none;
  text-decoration: none;
  }


a{
  text-decoration: none;
}
body{
    font-family: DM Sans;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.5;
    letter-spacing: 0.04em;
    @media screen and (min-width:768px) {
        font-size: 14px;
        line-height: 1.83;
    }
    @media screen and (min-width:1200px) {
        font-size: 16px;
    }
}

.container__contacts{
    
    width: 100vw;
    padding-bottom: 113px;
    margin-left: auto;
    margin-right: auto;
    padding:0 50px;
    @media screen and (min-width: 480px) {
      width: 480px;  
      
    }
    @media screen and (min-width: 768px) {
        width: 768px;
        padding: 0 36px;
        padding-bottom: 158px;
        
    }
    @media screen and(min-width: 1200px) {
        width: 1200px;
        padding:0 150px;
        padding-bottom: 165px;
    }
    @media screen and (max-width:767px) {
        max-width: 350px;
    }
}
.contacts{
    
    background-color: rgba(255, 245, 246, 1);
   
    
    background-repeat: no-repeat;
    
    background-size: contain;
    @media screen and (min-width: 1200px) {
        background-image:
        url(../images/dots.png);
    }
    
    
}
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);

}
.contacts__list{
    margin-bottom: 54px;
    
    @media screen and (min-width: 768px) {
        display: flex;
        margin-bottom: 84px;
    }
}
.contact{
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    &:first-child{
        margin-top: -84px;
    }
    flex-basis: 100%;
   
    padding: 43px 20px;
    background-color: #fff;
    border-radius: 10px;
    @media screen and (max-width:767px) {
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0px;
        }
    }
    @media screen and (min-width: 768px) {
        flex-basis: calc(100%/3);
        padding: 50px 31px;
        margin-right: 30px;
        margin-top: -78px;
            &:first-child{
                margin-top: -78px;
            }
             &:last-child{
                margin-right: 0px;
                
    }

    }
    @media screen and (min-width: 1200px) {
        &:first-child{
            margin-top: -102px;
        }
        padding: 51px 44px 62px 44px;
        margin-top: -102px;
        
    }

}
.category{
    
    color: #fff;
    display: inline-block;
    padding: 2px 15px;
    background-color: rgba(240, 209, 165, 1);
    border-radius: 4px;
    margin-bottom: 25px;
    @media screen and (min-width:768px) {
        margin-bottom: 30px;   
    }
    @media screen and (min-width:1200px) {
        margin-bottom: 32px;   
    }
}
.category--color{
    color: #fff;
    display: inline-block;
    padding: 2px 15px;
    background-color: rgba(194, 226, 151, 1);
    border-radius: 4px;
    margin-bottom: 25px;
    @media screen and (min-width:768px) {
        margin-bottom: 30px;   
    }
    @media screen and (min-width:1200px) {
        margin-bottom: 32px;   
    }


}
.contacts__header{
    
    margin-bottom: 10px;
}
.contact-container{
    position: relative;
}

.contacts__text{
    color: #907E82;
    margin-bottom: 50px;
    @media screen and (min-width:768px) {
        margin-bottom: 60px;
    }
    @media screen and (min-width:1200px) {
        margin-bottom: 64px;
        
    }
    &::after{
        position: absolute;
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background-color: #E1E1E1;
        bottom: 190px;
        left: 0px;
        @media screen and (min-width:768px) {
            bottom: 265px;
        }
    }
}
.agenda{
    margin-top: 25px;
    border-bottom: #E1E1E1;
    margin-bottom: 40px;
    &::after{
        position: absolute;
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background-color: #E1E1E1;
        left: 0px;
        bottom: 60px;
        @media screen and (min-width:768px) {
            bottom: 73px;
        }
    }

}
.agenda__days{
    margin-top: 9px;
    color: #000;
}
.agenda__hours{
    margin-bottom: 15px;
    color: #FFA5BA;
}
.contact__itam{
    color: #907E82;
}
.button-section{
    display: flex;
    flex-direction: column;
    align-items: center;
    @media screen and (min-width:768px) {
       flex-direction: row;
       justify-content: center;
    }
    
    
}
.button-section__itam--red{
    font-weight: 700;
    font-size: 16px;
   
    width: 162px;
    height: 44px;
    background-color: rgba(212, 19, 66, 1);
    color: #fff;
    border: 0;
    border-radius: 100px;
    @media screen and (min-width:1200px) {
        width: 198px;
    }
    &:hover{
        background-color: rgba(199, 19, 62, 1);
    }
    @media screen and (max-width:767px) {
        margin: 0 auto;
        
    }
}
.button-section__itam--white{
    font-weight: 700;
    font-size: 16px;
    
    width: 162px;
    height: 44px;
    border: 0;
    border-radius: 100px;
    color: #D41443;
    background-color: #fff;
    
    @media screen and (min-width:1200px) {
        width: 198px;
    }
    &:hover,
    &:focus{
        background-color: #ECEBEB;
    }
    @media screen and (max-width:767px) {
        margin-top: 10px;  
    }
    @media screen and (min-width:768px) {
        margin-left: 15px;
    }

}
